import React from "react"
import { AnimationOnScroll } from "react-animation-on-scroll"
import Livro from '../images/livro-removebg.png'

import { Wrap ,Titulo,Texto,LivroImage} from "./styles"

const Hero = () => {
 
   return(
    

   <Wrap id="hero">
  <AnimationOnScroll initiallyVisible={true}  duration={2} offset={330} animatePreScroll={false} animateOut="animate__fadeOut" animateIn="animate__fadeIn" >
          < Titulo><br/>Ol, seja bem-vindo (a)!</Titulo>
         <Texto > um prazer te receber! <br/>
         S de estar aqui tenha a certeza que j deu um grande passo<br/>
         para cuidar da sua sade mental
         <br/>Pode ter certeza que juntos faremos avanos significativos!
         </Texto>
         </AnimationOnScroll>
         <LivroImage src={Livro}/>
         
 </Wrap>

 

)}
export default Hero;